
@import "../../node_modules/dc/dc.css";
.result-view {
  width: 550px;
  height: 470px;
  text-align: left;
  font-size: 13px;
}
#rowChartDataTable {
  width: 490px;
  height: 235px;
  margin-top: -10px;
  margin-right: 40px;
  margin-left: 20px;
  word-wrap: break-word;
  float: left;
}
#try {
  word-wrap: break-word;
}
.row {
  float: left;
  text-align: center;
  margin-right: 10px;
}
.grid {
  overflow: hidden;
}
.bar {
  margin-left: 20px;
}
#json {
  overflow-x: scroll;
  overflow-y: scroll;
}
#block {
  display: block;
}
.dc-grid-group {
  visibility: collapse;
  margin-bottom: -40px;
}
.title {
  display: block;
  text-align: center;
  font-size: 15px;
  font-weight: bold;
}




